.search-image {
  background: -webkit-gradient(
                     linear, 
                     left top, left bottom, 
                     from(rgba(127, 132, 135, .7)), 
                     to(rgba(127, 132, 135, .7))),
                     url("images/jomar-271602-unsplash-edited.png");
  background: -webkit-linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/jomar-271602-unsplash-edited.png");
  background: -o-linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/jomar-271602-unsplash-edited.png");
  background: linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/jomar-271602-unsplash-edited.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  background-size: cover;
  overflow-y: hidden;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.search-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 10px 50px 15px 50px;
  padding: 10px 0;

}

.search-form-inputs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.center-button {
  display: block;
  margin: 0 auto;
  color: black;
  background-color: #51AC7E;
}

.save-button.disabled {
  pointer-events: auto;
}

.center-button:hover {
  background-color: #81CAA5
}

.error {
  color: darkred;
}

.search-error {
  text-align: center;
}

.search-container {
  background: rgba(210, 215, 217, 0.5);
  padding: 10px;
  margin: 20px 0;
  position: relative;
  z-index: 1;
}

.input-field label {
  color: black !important;
}

.input-field underline {
  border-bottom: .3px solid #000 !important
}

.input-field input:focus + label {
  color: #1E5373 !important;
}

.input-field input:focus {
  border-bottom: .3px solid #1E5373 !important;
  -webkit-box-shadow: 0 .3px 0 0 #000 !important;
          box-shadow: 0 .3px 0 0 #000 !important;
}

.input-field input {
  border-bottom: .3px solid #000 !important;
  -webkit-box-shadow: 0 .3px 0 0 #000 !important;
          box-shadow: 0 .3px 0 0 #000 !important;
}

.modal-waiting {
  position: relative;
  margin: 0 auto;
  -webkit-transform: translate(0, 35vh);
      -ms-transform: translate(0, 35vh);
          transform: translate(0, 35vh);
  width: 300px;
  height: 150px;
  background-color: white;
  z-index: 1000;
  text-align: center
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(72,72,72,.5);
  z-index: 20;
}

.spinner {
  display: block;
  margin: 33px auto 0 auto;
}



