.register-container {
  background: rgba(210, 215, 217, 0.7);
  padding: 20px;
  margin: 15vh 0;
}

.register-background {
  background: -webkit-gradient(
                     linear, 
                     left top, left bottom, 
                     from(rgba(127, 132, 135, .7)), 
                     to(rgba(127, 132, 135, .7))),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background: -webkit-linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background: -o-linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background: linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  background-size: cover;
  overflow-y: hidden;
  height: calc(100vh - 160px);
}

.login-container {
  background: rgba(210, 215, 217, 0.7);
  padding: 20px;
  margin: 15vh 0;
}

.login-background {
