.image-home {
  background: -webkit-gradient(
                     linear, 
                     left top, left bottom, 
                     from(rgba(127, 132, 135, .7)), 
                     to(rgba(127, 132, 135, .7))),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background: -webkit-linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background: -o-linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background: linear-gradient(
                     rgba(127, 132, 135, .7), 
                     rgba(127, 132, 135, .7)),
                     url("images/brandon-mowinkel-211936-unsplash-edited.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  background-size: cover;
  overflow-y: hidden;
  height: calc(100vh - 160px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.textbox {
  height: 500px;
  width: 60%;
  -ms-flex-pack: center;
      justify-content: center;
  background: rgba(210, 215, 217, .6);
  position: relative;
  text-align: center;
}

.textbox ol {
  text-align: left;
}

.instruction-header {
  color: #0D3E5B;
}

.textbox p a {
  color: #0D3E5B;
}


